@import url(https://fonts.googleapis.com/css?family=Sofia);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}


:root {
  --light-color: #f4f4f4;
  --off-white: #eee;
  --dark-grey: #333;
}

body {
  font-family: 'PT Sans' sans-serif;
  background-color: #ffcdb2;
  margin: 0;
  color: #fff;
  line-height: 1.6;
  /* height: 100%;
  display: flex;
  flex-direction: column; */
}

a {
  text-decoration: none;
  color: #ccc;
}

/* Nav bar */
nav {
  position: fixed;
  top: 0;
  height: 30px;
  width: 100%;
  background: url(/SuperPortfolio/static/media/navbarphoto.cccc68ca.jpg) no-repeat center/cover;
  color: #eee;
  color: var(--off-white);
  padding-bottom: 20pt;
}

nav ul {
  padding: 0;
  margin: 0;
}

nav li {
  display: inline;  
  float: none;
  margin: 25px;
}

nav a {
  display: inline-block;
  width: 100px;
  text-align: center;
  text-decoration: none;
  padding: 10px 0;
  color: #eee;
  color: var(--off-white);
  font-size: large;
}

nav a:hover {
  color: #333;
  color: var(--dark-grey);
}

nav li:hover {
  background-color: #eee;
  background-color: var(--off-white);
}

.name {
  position: absolute;
  top: 168px;
  right: 156px;
  font-size: 58px
}

.around {
  display: flex; 
  padding: 10px;
}

.profileimage {
  margin-left: 45px;
  -webkit-margin-before: 175px;
          margin-block-start: 175px;
  border-radius: 50%;
  padding: 0px; 
  width: 300px;
  height: 300px;  
  align-items: stretch;
  border: 10px solid #6d6875;
}
  

.bio {
  position: absolute;
  height: 100px;
  margin-top: 250px;
  margin-left: 150px;
  right: 100px;
  width: 600px;
  padding: 10px;
  color: #eee;
  color: var(--off-white);
  background-color: #b5838d ;
}

.bio1 {
  position: absolute;
  height: 200px;
  margin-top: 380px;
  margin-left: 150px;
  right: 100px;
  width: 600px;
  padding: 10px;
  color: #eee;
  color: var(--off-white);
  background-color: #b5838d ;
}

.footer {
  height: 30px;
  width: 100%;
  background: url(/SuperPortfolio/static/media/navbarphoto.cccc68ca.jpg) no-repeat center/cover;
  color: #eee;
  color: var(--off-white);
  padding-bottom: 20pt;
  position: fixed;
  left: 0;
  bottom: 0;
  text-align: center;
}

.icons {
  display: flex;
  justify-content: center;
  border: 6px;
}

.projectwrap {
  margin-top: 90px;
  display: flexbox;
  margin-left: 45px;
  margin-right: 45px;
}


.shows {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  text-align: left;
  margin-top: 55pt;
  margin-bottom: 55pt;
}

.projecth1 {
  display: flex;
  justify-content: center;
  font-size: 35px;
}

.shows h3 {
 font-size: 2rem;
 margin-bottom: 15pt;
}

.contact-container {
  margin-top: 5%;
  margin-left: 40%;
  margin-bottom: 10%;
}

.resume {
  margin-left: 40%;
}

.upper {
  position: relative;
  padding-bottom: 20px;
}

.lower{
  display: flex;
  display: inline-table;
}

.skill {
  font-size: 35px;
  margin-bottom: 35px;
}

.download {
  text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
  font-size: larger;
}

.li {
  font-size: 20px;
}

.link {
  text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
  border: 2px;
  padding: 5px;
}



